.urbangpt__possibility {
    display: flex;
    flex-direction: row;
}

.urbangpt__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 1rem;
}

.urbangpt__possibility-image img {
    width: 100%;
    height: 100%;
}

.urbangpt__possibility-content {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    flex-direction: column;
}

.urbangpt__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71e5f7;
}
.urbangpt__possibility-content h4:last-child {
    color: #ff8A71;
}

.urbangpt__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}

.urbangpt__possibility-content p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
    .urbangpt__possibility {
        flex-direction: column;

    }

    .urbangpt__possibility-image {
        margin: 1rem 0;
    }

    .urbangpt__possibility-image img {
        width: unset;
        height: unset;
    }

    .urbangpt__possibility-content {
        margin-top: 2rem;
    }
}

@media screen and (max-width: 700px) {
    .urbangpt__possibility-content {
         padding: 1rem 2rem;
        flex-direction: column;
        
    }
    .urbangpt__possibility-image img {
        width: 100%;
        height: 100%;
    }

}
