.urbangpt__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: var(--color-footer);
}

.urbangpt__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.urbangpt__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 75px;
}

.urbangpt__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}
.urbangpt__footer-btn:hover {
  
    border: 1px solid linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
    background: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
    
}

.urbangpt__footer-btn p {
    font-size: 18px;
    font-family: var(--font-family);
    line-height: 21px;
    color: #fff;
    word-spacing: 2px;
}

.urbangpt__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    text-align: left;
}

.urbangpt__footer-links div {
    width: 250px;
    margin: 1rem;
}

.urbangpt__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.urbangpt__footer-links_logo img {
    width: 118px;
    height: 30px;
  
}

.urbangpt__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 15px;
    color: #fff;
}

.urbangpt__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.urbangpt__footer-links_div h4 {
    font-size: 14px;
    line-height: 17px;
    font-family: var(--font-family);
    color: #fff;

    margin-bottom: 0.9rem;
}

.urbangpt__footer-links_div p {
    font-size: 12px;
    line-height: 15px;
    font-family: var(--font-family);
    color: #fff;

    margin: 0.5rem 0;
    cursor: pointer;
}

.urbangpt__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.urbangpt__footer-copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

@media screen and (max-width: 850px) {
    .urbangpt__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .urbangpt__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
       
    .urbangpt__footer-links {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
    }
    .urbangpt__footer-links_div {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .urbangpt__footer-links_logo img {
       display: flex;
       align-items: center;
       justify-content: center;
      
    }

    .urbangpt__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
    
}

@media screen and (max-width: 400px) {
    .urbangpt__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}