.urbangpt__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    padding: 3rem 5rem;
}

.urbangpt__features-heading {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;
}

.urbangpt__features-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
}

.urbangpt__features-heading p {
    color: #FF8A71;
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);

    margin-top: 2rem;
}

.urbangpt__features-container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 990px) {
    .urbangpt__features {
        flex-direction: column;
    }

    .urbangpt__features-heading {
        margin: 0 0 1rem 0;
    }
}


@media screen and (max-width: 550px) {
    .urbangpt__features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
    .urbangpt__features-heading {
        padding: 1rem 2rem;
      }
      .urbangpt__features-heading h1, .urbangpt__features-heading p {
          display: flex;
          align-items: center;
          justify-content: center;
        
        
      }
}